import React from 'react';
import { node, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-action-bar';

const ActionBar = ({ ariaLabel, children, className, role, type, ...remainingProps }) => (
  <div
    aria-label={ariaLabel}
    className={classnames('row', CSS_PREFIX, {
      [`${CSS_PREFIX}--${type}-controls`]: Boolean(type)
    }, className)}
    role={role}
    {...remainingProps}
  >
    {children}
  </div>
);

ActionBar.propTypes = {
  /**
   * @ignore
   */
  ariaLabel: string,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * @ignore
   */
  role: string,

  /**
   * Active
   */
  type: oneOf(['active', 'standard'])
};

ActionBar.defaultProps = {
  ariaLabel: 'action bar',
  role: 'region'
};

ActionBar.displayName = 'ActionBar';

export default ActionBar;
